	module.exports = {"data":{"site":{"siteMetadata":{"title":"Laura Domine","subtitle":"Physics Ph.D. student","copyright":"Powered by Gatsby. © All rights reserved.","menu":[{"label":"Blog","path":"/"},{"label":"Research","path":"/research"},{"label":"Projects","path":"/projects"},{"label":"About","path":"/about/"},{"label":"Contact","path":"/contact/"}],"author":{"name":"Laura Domine","email":"ldomine@stanford.edu","telegram":"#","twitter":"#","github":"Temigo","rss":"#","vk":"#"}}},"allMarkdownRemark":{"edges":[{"node":{"fields":{"slug":"/posts/aymara/","categorySlug":"/categories/languages/"},"frontmatter":{"title":"Aymara","date":"2018-02-03","category":"Languages","description":"The Altiplano is a fascinating place at the heart of  the Andes, the most extensive area of high plateau after Tibet. It was the birthplace of several major civilizations such as Tiwanaku and the Inca Empire. In this landscape made of snow-covered peaks, lakes, deserts, salt flats, and high plains, the Aymara people speak a fascinating language full of surprises."}}},{"node":{"fields":{"slug":"/posts/piraha/","categorySlug":"/categories/languages/"},"frontmatter":{"title":"The Pirahã language","date":"2017-10-28","category":"Languages","description":"Human languages share few features that distinguish them from other form of animal communication. One of the most famous is recursion. In any language one can always form an arbitrarily long sentence. Always? Not quite. According to Daniel Everett, up to now the only non-native of the Pirahã Amazonian tribe who has ever understood their language, the Pirahã language does have a sentence of maximal length. Of course this statement stirred a durable controverse among linguists and prompted Noam Chomsky to criticize him as a 'charlatan'. Yet his story is interesting and, in my opinion, worth listening to."}}}]}},"pathContext":{"category":"Languages"}}

